import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    show:0,
    isLogin:false,
    cartlist: [
      // {
      //   img: 'https://p1.maiyaole.com/img/971/971672/120_120.jpg?a=1127706105',
      //   qty: 1,
      //   price: '12.80',
      //   name: '  100ml',
      //   selected: true
      // }
    ],
    cartstate: "",
    CartDelComfirm:false,
  },
  mutations: {
    // 
    changeCart(state) {
      if (state.cartstate == "") {
        state.cartstate = "";
      }else{
        state.cartstate = "";
      }
    },
    // 
    changeQty(state, {guid,qty}) {
      state.cartlist = state.cartlist.map(function (goods) {
        if (goods.guid == guid) {
          goods.qty = qty;
        }
        return goods;
      })
    },
    addCartchangeQty(state, {guid,qty}) {
      state.cartlist = state.cartlist.map(function (goods) {
        if (goods.guid == guid) {
          goods.qty += qty;
        }
        return goods;
      })
    },
    // 
    select(state, guid) {
      state.cartlist = state.cartlist.map(function (goods) {
        if (goods.guid == guid) {
          goods.selected = !goods.selected;
        }
        return goods;
      })
    },
    // /
    allSelect(state) {
      state.cartlist = state.cartlist.map(function (goods) {
        goods.selected = true;
        return goods;
      });
    },
    notAllSelect(state) {
      state.cartlist = state.cartlist.map(function (goods) {
        goods.selected = false;
        return goods;
      });
    },
    // 
    confirmChange(state){
      state.CartDelComfirm=!state.CartDelComfirm;
    },
    // 
    cartdelete(state){
      state.cartlist = state.cartlist.filter(function(goods){
        if(goods.selected==false){
          return goods;
        }
      })
    },
    // 
    Login(state){
      state.isLogin=true;
    },
    Loginout(state){
      state.isLogin=false;
    },
    addToCart(state,goods){
      state.cartlist.push(goods)
    }
  },
  actions: {


  }
})